ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 2rem;
}
li {
    font-size: 1.5rem;
    font-weight: 600;
    color: #fff;
    cursor: pointer;
    transition: all 0.3s linear;
}
li a {
    padding: 5px;
    text-decoration: none;
    color: #000000;
}
.navbar {
    padding: 1rem;
}
