body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::selection {
  color: white;
  background-color: var(--blue);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  transition: all 0.3s ease-in-out;
}
:root {
  --blue : rgb(103, 174, 255);
}
#Underline {
  padding: 2px;
  width: 3em;
  background-color: var(--blue);
}
#PageTitle:hover + #Underline {
  width: 4em;
}

#PageTitle {
  font-size: 3em;
  font-weight: bold;
  color: var(--blue);
  margin: 0;
  padding: 0;
}
p {
  display: inline-block;
  vertical-align: top;
}
img {
  display: inline-block;
  margin: 5px;
  border-radius: 25px;
}
#NF h1 {
  color: var(--blue);
}
#NF {
  padding: 20px;
}
#NF #AWrap {
  width: fit-content;
  color: black;
  font-size: 2rem;
  font-weight: 500;
  text-decoration: none;
  border-bottom: 2px solid transparent;
  border-radius: 25px;
}
#NF #AWrap a {
  color: black;
  text-decoration: none;
  border-bottom: 2px solid transparent;
}
#NF #AWrap a:hover {
  border-bottom: 2px solid black;
}
#NF #AWrap a:active {
  transform: scale(0.95);
}
h2 {
  font-size: 1.75em;
  font-weight: bold;
  color: black;
  margin: 0;
  padding: 0;
}
