#ContactWrapper {
    padding-left: 20px;
    padding-right: 20px;
    height: fit-content;
}
#form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
label {
    font-size: 2em;
    font-weight: bold;
    color: black;
}
form {
    background-size: cover;
    padding: 20px;
    border-radius: 25px;
    margin: 20px;
    width: 80vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    background-image: url(./images/bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 20px;
    border-radius: 25px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}
form input {
    padding: 10px;
    font-size: 1.5rem;
    border-radius: 10px;
    border: 1px solid rgba(128, 128, 128, 0.377);
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}
form textarea {
    font-size: 1.5rem;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid rgba(128, 128, 128, 0.377);
    resize: none;
    width: 80%;
    height: 20vh;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
*:focus {
    outline: 2px solid rgba(255, 255, 255, 0.377);
    transform: scale(1.05);
}
textarea::-webkit-scrollbar {
    width: 0px;
    border-radius: 5px;
    z-index: -1;
}
textarea::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
textarea::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
}
textarea::-webkit-scrollbar-thumb:hover {
    background: #111;
}
form button {
    margin: 10px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: black;
    font-weight: bold;
    font-size: 2em;
    width: 80%;
}
form button:active {
    transform: scale(0.95);
}
#submitnotif {
    position: fixed;
    bottom: 2em;
    right: 20px;
    padding: 10px 20px;
    font-size: 2em;
    color: black;
    font-weight: bold;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
}