#HomeWrapper {
    padding-left: 20px;
    padding-right: 20px;
    height: fit-content;
}
#HomeTextWrapper {
    margin-top: 10px;
    background-image: url(./images/bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 20px;
    border-radius: 25px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}
#Wrappers {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 2fr));
    grid-gap: 10px;
}
#HomeTextWrapper #S1 {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    width: 1fr;
    padding: 10px;
}
#Wrappers a:active {
    transform: scale(0.95);
}
#HomeTextWrapper #S1 p {
    vertical-align: top;
    font-size: 2em;
    font-weight: 500;
    text-decoration: none;
    color: #333;
    padding: 0;
    margin: 0;
}
#HomeTextWrapper #S1 img {
    height: 2.5em;
    border-radius: 0 !important	;
}
#HomeTextWrapper h1 {
    border-bottom: 2px solid black;
}
#HomeTextWrapper #S1:hover {
    background: rgba(255, 255, 255, 0.4);
}
